body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: #d9dadb;
    font-family: 'Raleway', sans-serif }

header {
    background-color: #004fa7;
    margin: 0;
    padding: 15px 30px 70px;
    text-align: center;
    .text { 
        width: 60%; 
        margin: auto;
        /* Extra Small */
        @media (max-width: 575.98px) { width: 85% }
        /* Small */
        @media (min-width: 576px) and (max-width: 767.98px) { width: 80% }
        /* Medium */
        @media (min-width: 768px) and (max-width: 991.98px) { width: 75% }
        /* Large  */
        @media (min-width: 992px) and (max-width: 1199.98px) { width: 70% }
    }
    h1 {
        font-size: 48px;
        font-weight: bold;
        color: #f7f938;
        margin: 0;
        padding: 0;
        padding-bottom: 10px;
        /* Extra Small */
        @media (max-width: 575.98px) { font-size: 27px }
        /* Small */
        @media (min-width: 576px) and (max-width: 767.98px) { font-size: 30px }
        /* Medium */
        @media (min-width: 768px) and (max-width: 991.98px) { font-size: 35px }
        /* Large  */
        @media (min-width: 992px) and (max-width: 1199.98px) { font-size: 42px }
    }
    p {
        color: #fff;
        font-size: 38px;
        line-height: 45px;
        font-weight: 300;
        margin: 0;
        padding: 0;
        /* Extra Small */
        @media (max-width: 575.98px) {
            font-size: 20px;
            line-height: 30px;
        }

        /* Small */
        @media (min-width: 576px) and (max-width: 767.98px) {
            font-size: 20px;
            line-height: 30px;
        }

        /* Medium */
        @media (min-width: 768px) and (max-width: 991.98px) {
            font-size: 27px;
            line-height: 37px;
        }

        /* Large  */
        @media (min-width: 992px) and (max-width: 1199.98px) {
            font-size: 35px;
            line-height: 43px;
        }
    }
}

.discover {
    width: 50%;
    margin: auto;
    padding: 25px 50px 37px;
    background-color: #fff;
    border-radius: 10px;
    margin-top: -50px;
    overflow: hidden;
    /* Extra Small */
    @media (max-width: 575.98px) { width: 65% }
    /* Small */
    @media (min-width: 576px) and (max-width: 767.98px) { width: 65% }
    /* Medium */
    @media (min-width: 768px) and (max-width: 991.98px) { width: 60% }
    /* Large  */
    @media (min-width: 992px) and (max-width: 1199.98px) { width: 55% }
    h2 {
        text-align: center;
        margin: 0 0 30px;
        font-size: 30px;
        line-height: 40px;
        /* Extra Small */
        @media (max-width: 575.98px) { font-size: 20px; line-height: 30px }
        /* Small */
        @media (min-width: 576px) and (max-width: 767.98px) { font-size: 20px; line-height: 30px }
        /* Medium */
        @media (min-width: 768px) and (max-width: 991.98px) { font-size: 25px; line-height: 35px }
        /* Large  */
        @media (min-width: 992px) and (max-width: 1199.98px) { font-size: 27px; line-height: 37px }
    }
    form {
        .quiz {
            padding: 20px;
            background-color: #004fa7;
            margin: 20px;
            border-radius: 10px;
            color: #fff;
            font-size: 18px;
            font-weight: bold;
            text-transform: capitalize;
            /* Extra Small */
            @media (max-width: 575.98px) { font-size: 15px }
            /* Small */
            @media (min-width: 576px) and (max-width: 767.98px) { font-size: 15px }
            /* Medium */
            @media (min-width: 768px) and (max-width: 991.98px) { font-size: 16px }
            /* Large  */
            @media (min-width: 992px) and (max-width: 1199.98px) { font-size: 17px }
        }
    }
}
